.i {
    display: flex;
    height:auto;
    
}

.i-left {
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.i-right {
    flex: 1 1;
    padding-top: 90px;
    
}
.i-left-wrapper{
    padding: 50px;
    height: 50%;
    display:flex;
    flex-direction: column;
    justify-content:space-between;
}
.i-intro{
    font-size: 30px;
    font-weight: 300;
    
}

.i-name{
    font-size: 60px;
    font-family: 'Poppins', sans-serif;
   
}

.i-title{
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper{
    height: 100%;
    -webkit-animation: move 10s ease-in-out infinite alternate;
            animation: move 10s ease-in-out infinite alternate;
}

@-webkit-keyframes move {
    25% {
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }
    50% {
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
    }
    75% {
        -webkit-transform: translateY(-150px);
                transform: translateY(-150px);
    }
    100% {
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
    }
}

@keyframes move {
    25% {
        -webkit-transform: translateY(-50px);
                transform: translateY(-50px);
    }
    50% {
        -webkit-transform: translateY(-100px);
                transform: translateY(-100px);
    }
    75% {
        -webkit-transform: translateY(-150px);
                transform: translateY(-150px);
    }
    100% {
        -webkit-transform: translateY(-200px);
                transform: translateY(-200px);
    }
}

.i-title-item{
    height: 50px;
    font-size: 30px;
    font-weight: bold;
    color: #ea417f;
    display: flex;
    align-items: center;
}
.i-button{
    border: none;
    background-color: #ea417f;
    color:white;
    width: 150px;
    height: 50px;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
    align-items: center;
}

.i-img{

    background-size: contain;
}

@media screen and (max-width: 480px) {
    .i {
        flex-direction: column;
        text-align: center;
        width:50%;
    }

    .i-left-wrapper {
        padding-top: 150px;
        padding-left: 400px;
        width: 100%;  
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
    }
    .i-desc{
      display: none;
    }
    .i-button {
        margin-top:50px

    }

}



.a{
    height: 800px;
    display: flex;
    align-items: center;
}

.a-left{
    flex: 1 1;
    padding-top: 90px;
    
}
.a-title{
    padding: 20px 0px 20px 0px;
}
.a-desc{
    padding: 20px 0px 20px 0px;
}

.a-right{
    flex: 1 1;
}
.a-img{
    background-size: contain;
}

@media screen and (max-width: 480px) {
    .a {
        flex-direction: column;
        text-align: center;
    }

    .a-left {
        padding-top: 150px;
        width: 100%;  
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 100%;
    }
    .a-right{
      padding: 50px;
    }

}





.header{
    width: 100%;
    height: 70px;
    position: fixed;
    top:0px;
    padding: 10px 10Px;
    z-index:2;
    background-color: #fff;

}
.wrapper{
    padding: 10px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.left{
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.right{
    display: flex;
    align-items: center;
    

}
@media screen and (max-width: 600px) {
    .right.responsive {position: relative;}
    .right.responsive a.icon {
      position: absolute;
      right: 0;
      top: 0;
    }
    .right.responsive a {
      float: none;
      display: block;
      text-align: left;
    }
  }
.flex-row {
    display:flex;
    flex-wrap: wrap;
    text-align: right;
    float:right;
    justify-content: space-between;
    
  }
  @media screen and (max-width: 600px) {
    .topnav.responsive {position: relative;}
    .topnav.responsive a.icon {
      position: absolute;
      right: 0;
      top: 0;
    }
    .topnav.responsive a {
      float: none;
      display: block;
      text-align: left;
    }
  }
.footer {
    background-color: #ea417f;
    flex-flow: row nowrap;
    text-align: center;
    padding: 15px 0px;
    
    
  }
  
@media screen and (max-width: 480px) {
  .footer {
    display: none;
  }
  
  }



.portfolio{
    padding: 50px 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.p-title{
    padding: 20px;
}

.p-list{
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

@media screen and (max-width: 480px) {
    .p-list {
        flex-direction: column;
        width: 100%;
        padding-bottom: 30px;
        flex-basis: 480px;
    }

    .portfolio {
        width: 100%;  
        align-items: center;
        justify-content: center;
        text-align: center;
        height: 20vh;
        display:block;
        flex-direction: column;
    }
    .p-title{
      padding-top: 50px;
      padding-right:200px;
      text-align: center;
      
    }
}

.proj {
    width: 30%;
    height: 40vh;
    margin: 20px 10px;
    border: 2px solid #ccc;
    border-radius: 10px 10px 0px 0px;
    overflow: hidden;
}
.proj-browser {
    height: 20px;
    background-color: #00B4D8;
    display: flex;
    align-items: center;
}

.proj-circle{
    width: 6px;
    height: 6px;
    border-radius: 50%;
