    background-color: #ea417f;

}
.proj-title{
    margin-top: 10px;
}

.proj-img{
    width: 100%;
}

@media screen and (max-width: 480px) {
    .proj {
        flex-direction: column;
        text-align: center;
        width: 100%;
        height: auto;
    }
    
}

